import React from 'react'
import './Header.css'
import CTA from './CTA'
import Me from '../../assets/Me.png'
import HeaderSocials from './HeaderSocials'

const Header = () => {
  return (
    <header>
      <div className="container header__container">
        <h5>Hello I'm</h5>
        <h1>Jaynish Mandalia</h1>
        <h5 className="text-light">Problem Solver, Flutter Enthusiast, Competitive Programmer</h5>
        <CTA />
        <HeaderSocials/>
        
        <div className="me">
          <img src={Me} alt="me" />
        </div>
        <a href="#contact" className='scroll_down'>Scroll Down</a>
      </div>
    </header>
  )
}

export default Header